.ProductDetails {
    background-color: white;
    width: 100vw;
    max-width: 100%;
    padding: 6vmax;
    box-sizing: border-box;
    display: flex;
}

.ProductDetails > div {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 2vmax;
    box-sizing: border-box;
    border: 1px solid white;
}


.ProductDetails > div:last-child {
    align-items: flex-start;
}

.CarouselImage {
    width: 20vmax;
}

.detailsBlock-1 > h2 {
    color: rgb(54,54,54);
    font: 600 1.6vmax "Roboto";
}

.detailsBlock-1 > p {
    color: rgba(54, 54, 54, 0.58);
    font: 200 0.6vmax cursive;
}

.detailsBlock-2 {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-top: 1px solid rgba(0, 0, 0, 0.2);
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    width: 70%;
    padding: 1vmax 0;
}

.detailsBlock-2-span {
    color : rgba(54, 54, 54, 0.58);
    font: 200 1vmax cursive;
    padding-top: 0.2vmax;
    margin-left: 0.5vmax;
}

.detailsBlock-3 {
    width: 70%;
}

.detailsBlock-3 > h1 {
    color: rgba(17, 17, 17, 0.8);
    font: 400 1.8vmax "Franklin Gothic Medium";
    margin: 1vmax 0;
}

.detailsBlock-3-1 {
    display: flex;
    align-items: center;
}

.detailsBlock-3-1-1 > button {
    border: none;
    background-color: rgba(0, 0, 0, 0.6);
    padding: 0.5vmax 0.7vmax;
    cursor: pointer;
    color: white;
    transition: all 0.5s;
}

.detailsBlock-3-1-1 > button:hover {
    background-color: rgba(0,0,0,0.8);
}

.detailsBlock-3-1-1 > input {
    border: none;
    padding: 0.5vmax 1vmax;
    width: 1vmax;
    text-align: center;
    outline: none;
    font: 400 0.8vmax "Roboto";
    color: rgba(0, 0, 0, 0.75);
    padding-bottom: 0.57vmax;
}

.detailsBlock-3-1 > button:last-child {
    border: none;
    cursor: pointer;
    color: white;
    transition: all 0.5s;
    background-color: tomato;
    font: 500 0.7vmax "Roboto";
    border-radius: 20px;
    padding: 0.5vmax 2vmax;
    margin: 1vmax;
    outline: none;
}

.detailsBlock-3-1 > button:last-child:hover {
    background-color: rgb(214, 84, 61);
}

.detailsBlock-3 > p {
    border-top: 1px solid rgba(0, 0, 0, 0.2);
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    padding: 1vmax 0;
    color: rgba(0, 0, 0, 0.6);
    font: 400 1vmax "Roboto";
    margin: 1vmax 0;
}

.detailsBlock-4 {
    margin-top: 0.4vmax;
    color: rgba(0, 0, 0, 0.9);
    font: 500 1.2vmax sans-serif;
}

.detailsBlock-4 > p{
    margin-top: 0.4vmax;
    color: rgba(0, 0, 0, 0.6);
    font: 300 0.8vmax sans-serif;
}

.submitReview {
    border: none;
    background-color: tomato;
    font: 500 0.7vmax "Roboto";
    border-radius: 20px;
    padding: 0.6vmax 2vmax;
    margin: 1vmax 0;
    color: white;
    cursor: pointer;
    transition: all 0.5s;
    outline: none;
}

.submitReview:hover {
    background-color: rgb(214, 84, 61);
    transform: scale(1.05);
}

.submitDialog {
    display: flex;
    flex-direction: column;
}


.submitDialogTextArea {
    border: 1px solid rgba(0, 0, 0, 0.1);
    margin: 1vmax 0;
    outline: none;
    padding: 1rem;
    font: 300 1rem "Roboto"
}

.reviewsHeading {
    color: #000000be;
    font: 500 1.4vmax "Roboto";
    text-align: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.23);
    padding: 1vmax;
    width: 20vmax;
    margin: auto;
    margin-bottom: 4vmax;
}

.reviews {
    display: flex;
    overflow: auto;
}

.noReviews {
    font: 400 1.3vmax "Gill Sans";
    text-align: center;
    color: rgba(0, 0, 0, 0.548)
}

@media screen and (max-width: 600px) {
    .ProductDetails {
        flex-direction: column;
        height: unset;
    }

    .ProductDetails > div:last-child {
        align-items: center;
    }

    .detailsBlock-1 > h2 {
        font-size: 2.8vmax;
        text-align: center;
    }

    .detailsBlock-1 > p {
        text-align: center;
        font-size: 1.5vmax;
    }

    .detailsBlock-2 {
        justify-content: center;
    }

    .detailsBlock-2 > span{
        padding-top: 0.7vmax;
        font-size: 1.5vmax;
    }

    .detailsBlock-3 > h1 {
        font: 700 3vmax "Franklin Gothic Medium";
        text-align: center;
    }

    .detailsBlock-3-1 {
        flex-direction: column;
    }

    .detailsBlock-3-1-1 {
        padding: 2vmax 0;
    }

    .detailsBlock-3-1-1 > button {
        padding: 1.2vmax;
        width: 4vmax;
        height: 5vmax;
    }

    .detailsBlock-3-1-1 > input {
        padding: 1.5vmax;
        width: 3vmax;
        font: 400 1.6vmax "Roboto";
        height: 2vmax;
        padding-bottom: 1.4vmax;
        padding-top: 1.56vmax;
    }

    .detailsBlock-3-1 > button:last-child {
        font: 500 1.7vmax "Roboto";
        padding: 1.5vmax;
        width: 20vmax;
        margin: 3vmax 0;
    }

    .detailsBlock-3 > p {
        padding: 2.5vmax 0;
        text-align: center;
        font: 400 2vmax "Roboto";
    }

    .detailsBlock-4 {
        font: 500 2.5vmax sans-serif;
        padding: 1.6vmax;
    }

    .detailsBlock-4 > p {
        font: 300 1.8vmax sans-serif;
    }

    .submitReview {
        font: 500 1.7vmax "Roboto";
        padding: 1.5vmax;
        width: 20vmax;
        margin: 3vmax 0;
    }

}