.dashboard {
    width: 100vw;
    max-width: 100%;
    display: grid;
    grid-template-columns: 1fr 5fr;
    position: absolute;
}

.newProductContainer {
    width: 100%;
    box-sizing: border-box;
    background-color: rgb(231, 231, 231);
    border-left: 1px solid rgba(0, 0, 0, 0.201);
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.newProductContainer h1 {
    color: rgba(0, 0, 0, 0.62);
    font: 300 2rem "Roboto";
    text-align: center;
}

.createProductForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 3vmax;
    justify-content: space-evenly;
    height: 70%;
    width: 50vh;
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.201);
}

.createProductForm > div {
    display: flex;
    width: 100%;
    align-items: center;
}


.createProductForm > div > input, 
.createProductForm > div > select, 
.createProductForm > div > textarea {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.27);
    border-radius: 4px;
    font: 300 0.9vmax cursive;
    outline: none;
}

.createProductForm > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.62);
}

#createProductFormFile > input {
    display: flex;
    padding: 0;
}

#createProductFormFile > input::file-selector-button {
    cursor: pointer;
    width: 100%;
    z-index: 2;
    height: 5vh;
    border: none;
    margin: 0;
    font: 400 1vmax cursive;
    transition: all 0.5s;
    padding: 0 1vmax;
    color: rgba(0, 0, 0, 0.62);
    background-color: white;
}

#createProductFormFile > input::file-selector-button:hover {
    background-color: rgb(235, 235, 235);
}

#createProductFormImage {
    width: 100%;
    overflow: auto;
}

#createProductFormImage > img {
    width: 3vmax;
    margin: 0 0.5vmax;
}

#createProductBtn {
    border: none;
    background-color: tomato;
    color: white;
    font: 300 1.2vmax "Roboto";
    width: 100%;
    padding: 0.9vmax;
    cursor: pointer;
    transition: all 0.5s;
    text-transform: uppercase;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.22);
}

#createProductBtn:hover {
    background-color: rgb(179, 66, 46);
} 


@media screen and (max-width: 600px){
    .dashboard {
        grid-template-columns: 1fr;
    }

    .newProductContainer {
        background-color: white;
    }

   .createProductForm {
        padding: 5vmax;
    }

    .createProductForm > div > input, 
    .createProductForm > div > select, 
    .createProductForm > div > textarea {
        padding: 2.5vmax 5vmax;
        font: 300 1.7vmax cursive;
    }

    .createProductForm > div > svg {
        font-size: 2.8vmax;
    }

    #createProductFormImage > img {
        width: 8vmax;
    }

    #createProductFormFile > input::file-selector-button {
        height: 7vh;
        font: 400 1.8vmax cursive
    }

    #createProductBtn {
        font: 300 1.9vmax "Roboto";
        padding: 1.8vmax;
    }

}