.sidebar {
    background-color: white;
    display: flex;
    flex-direction: column;
    padding: 4rem 0;
}

.sidebar > a:first-child {
    padding: 0;
}

.sidebar > a > img {
    width: 100%;
    transition: all 0.5s;
}

.sidebar > a > img:hover {
    filter: drop-shadow(0 0 10px tomato)
}

.sidebar > a {
    text-decoration: none;
    color: rgba(0, 0, 0 , 0.5);
    padding: 2rem;
    transition: all 0.5s;
    font: 200 1rem "Roboto"
}

.sidebar a:hover {
    color: tomato;
}

.sidebar > a > p {
    display: flex;
    align-items: center;
}

.sidebar > a > p > svg {
    margin-right: 0.5rem;
}

.MuiTypography-root {
    background-color: white !important;
}