.cartItemCard {
    display: flex;
    padding: 1vmax;
    height: 8vmax;
    align-items: flex-start;
    box-sizing: border-box;
}

.cartItemCard > img {
    width: 5vmax;
}

.cartItemCard > div {
    display: flex;
    flex-direction: column;
    margin: 0.3vmax 1vmax;
}

.cartItemCard > div > a {
    font: 300 0.9vmax cursive;
    color: rgba(24, 24, 24, 0.82);
    text-decoration: none;
}

.cartItemCard > div > span {
    font: 300 0.9vmax "Roboto";
    color: rgba(24, 24, 24, 0.82);
}

.cartItemCard > div > p {
    color: tomato;
    cursor: pointer;
    font: 100 0.8vmax "Roboto";
}

@media screen and (max-width: 600px) {
    .cartItemCard {
        padding: 3vmax;
        height: 25vmax;
    }

    .cartItemCard > img {
        width: 10vmax;
    }

    .cartItemCard > div {
        margin: 1vmax 2vmax;
    }

    .cartItemCard > div > a {
        font: 300 2vmax cursive;
    }

    .cartItemCard > div > span {
        font: 400 1.9vmax "Roboto";
    }

    .cartItemCard > div > p {
        font: 100 1.8vmax "Roboto";
    }
}