.shippingConatiner {
    width: 100vw;
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .shippingBox {
    background-color: white;
    width: 25vw;
    height: 90vh;
    box-sizing: border-box;
    overflow: hidden;
  }

  .shippingHeading {
    text-align: center;
    color: rgba(0, 0, 0, 0.65);
    font: 400 2.5vmax "Roboto";
    padding: 2.5vmax;
    width: 50;
    margin: auto;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  }

  .shippingForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    margin: auto;
    padding: 2vmax;
    height: 80%;
    transition: all 0.5s;
  }

  .shippingForm > div {
    display: flex;
    width: 100%;
    align-items: center;
  }

  .shippingForm > div > input,
  .shippingForm > div > select
  {
    padding: 1vmax 4vmax;
    padding-right: 2vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.25);
    border-radius: 4px;
    font: 300 0.9vmax cursive;
    outline: none;
  }

  .shippingForm > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.7vmax;
    color: rgba(0, 0, 0, 0.65);
  }

.shippingBtn {
    border: none;
    background-color: tomato;
    color: white;
    font: 300 1vmax "Roboto";
    width: 100%;
    padding: 1vmax;
    cursor: pointer;
    transition: all 0.5s;
    outline: none;
    margin: 2vmax;
}

.shippingBtn:hover {
    background-color: rgb(179, 66, 46);
}

@media screen and (max-width: 600px) {
    .shippingBox {
        width: 100vw;
        height: 95vh;
    }

    .shippingHeading {
        font: 400 6vw "Roboto";
        padding: 5vw;
    }

    .shippingForm {
        padding: 11vw;
    }

    .shippingForm > div > input,
    .shippingForm > div > select {
        padding: 5vw 10vw;
        font: 300 4vw cursive;
    }

    .shippingForm > div > svg {
        font-size: 6vw;
        transform: translateX(3vw);
    }

    .shippingBtn {
        font: 300 4vw "Roboto";
        padding: 4vw;
    }
}